import {
	hostUrl
} from '@/http/request.js';
import {cartDel} from '@/http/cart.js';
export default {
	props: {
		cartList: {
			type: Array,
			default: () => {
				return []
			}
		}
	},
	data(){
		return {
			hostUrl:hostUrl
		}
	},

	methods:{
		delItem(id){
			cartDel(id).then(res=>{
				if(res.data !== null && res.data.status === 0){
					this.$emit('changeCart');
					uni.showToast({
						title:'删除成功'
					})
				}else{
					uni.showToast({
						title:'删除失败'
					})
				}
			})
		},
		choice(e){
			let {
				id,
				checkid
			} = e.currentTarget.dataset;
			
			this.$emit('choice',{id,checkId:checkid})
		}
	}
}
